.container {
  padding: 20px;
  background-color: #ffffff;
  min-height: 100vh;
  font-family: 'Arial', sans-serif;
}

.top-bar {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}

.back-button {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #000;
}

h2 {
  font-size: 18px;
  font-weight: bold;
  margin: 0;
}

.lista {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.reserva {
  background-color: #f2f2f2;
  border-radius: 12px;
  padding: 16px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}
